.question {
  background: #fefefe;
  border-radius: 0.5rem;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.4);
  padding: 1.5rem;

  /* HACKZIN -> A PARTIR DA SEGUNDA QUESTION, ADICIONE UM MARGIN TOP */
  & + .question {
    margin-top: .5rem;
  }

  &.highlighted {
    background: #f4f0ff;
    border: 1px solid #835afd;
    
    footer .user-info span {
      color: #29292e;
    }
  }
  
  &.answered {
    background: rgba(128, 128, 128, 0.308);
  }

  p{
    color: #29292e;
  }

  footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1.5rem;


    .user-info {
      display: flex;
      align-items: center;

      img {
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
      }

      span {
        margin-left: 0.5rem;
        color: #737380;
        font-size: 0.875rem;
      }
    }

    > div {
      display: flex;
      gap: 1rem;
    }

    button {
      border: 0;
      background: transparent;
      cursor: pointer;
      transition: filter 0.2s;

      &.like-button{
        display: flex;
        align-items: flex-end;
        color: #737380;
        gap: 0.5rem;

        &.liked {
          color: #835afd;

          svg path{
            stroke: #835afd;
          }
        }
      }

      &:hover{
        filter: brightness(0.8);
      }
    }
  }
}